$imgPath: "/images";

body {
  height: 100vh;
  // 중첩 변수 처리
  background-image: url("#{$imgPath}/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container {
  padding: 50px 0;

  .heros {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 700px;
    /* 블럭요소가 좌우 값이 auto라면 가운데 정렬한다. */
    margin: 0 auto;
    padding: 40px 20px;

    .hero {
      width: 80px;
      height: 84px;
      margin: 4px;
      border: 3px solid #fff;
      border-radius: 10px;
      box-sizing: border-box;
      background-color: #555;
      transform: skewX(-14deg);
      transition:
        tranform .1s,
        background-color .6s;
      overflow: hidden;

      &:hover {
        background-color: #ff9c00;
        transform: scale(1.3) skewX(-14deg);
        z-index: 1;
      }

      .image {
        width: 140%;
        height: 100%;
        background-position: center;
        background-size: 90px;
        background-repeat: no-repeat;
        transform: skewX(14deg) translateX(-16px);
      
      }
      
      @for $i from 1 through 32 {
        // .hero
        &:nth-child(#{$i}) .image {
          background-image: url("#{$imgPath}/hero#{$i}.png");
        }
      }
      
    }
  }
  .logo {
    max-width: 300px;
    margin: 0 auto;
    padding: 0 20px;
  
    img {
      width: 100%;
    }
  }
}



